import React, { useState, useEffect, useRef } from "react";
import {
  Container,
  Typography,
  TextField,
  Divider,
  Button,
  Grid,
  Stack,
} from "@material-ui/core";
import { Box } from "@material-ui/system";
import { styled } from "@material-ui/styles";
import { useSelector } from "react-redux";
import { UseQueryResult, useQuery } from "react-query";
import {
  FacebookShareButton,
  FacebookIcon,
  FacebookMessengerShareButton,
  FacebookMessengerIcon,
  WhatsappShareButton,
  WhatsappIcon,
  TwitterShareButton,
  TwitterIcon,
  TelegramShareButton,
  TelegramIcon,
} from "react-share";

import {
  RootAppState,
  CreateProfileResponseNgoOrDonee,
  InvitationType,
  AddFunction,
} from "../../../../../utils/typeHelpers";
import { APP_URL, SEND_INVITATION_LINK } from "../../../../../utils/Api";
import Loader from "../../../../../components/Loader";
import { useMutation, UseMutationResult } from "react-query";
import { MessageError } from "../../../../../components/AlertMessageError";
import { MessageSuccess } from "../../../../../components/AlertMessageSuccess";

import ErrorCatchComponent from "../../../../../components/ErrorCatchComponent";
import ApiErrorPage from "../../../../Errror/ApiErrorPage";
import useAxios from "../../../../../utils/useAxios";

// ----------------------------------------------------------------------

export default function InviteDonee() {
  const userState = useSelector((state: RootAppState) => state.AuthReducer);

  const [inviteLink, setInviteLink] = useState("");
  const [inviteMail, setInviteMail] = useState<any>("");
  const [inviteWhatsapp, setInviteWhatsapp] = useState<any>("");

  // const {
  //   data,
  //   isLoading,
  //   isSuccess,
  // }: UseQueryResult<CreateProfileResponseNgoOrDonee, Error> = useQuery(
  //   ["doneorngoProfile", userState.userInfo.access],
  //   () => GET_DONEEORNGO_PROFILE_API(userState.userInfo.access)
  // );

  const axiosInstance = useAxios();
  const {
    data,
    isLoading,
    isError,
    error,
    isSuccess,
  }: UseQueryResult<CreateProfileResponseNgoOrDonee, Error> = useQuery(
    ["doneorngoProfile", userState.userInfo.access],
    async () => {
      const response = await axiosInstance.get(`/profile-detail/`);
      return response.data;
    }
  );

  useEffect(() => {
    if (data?.invitation_id !== undefined) {
      setInviteLink(`${APP_URL}/verifydoneeinvite/id=${data?.invitation_id}`);
    }
  }, [data?.invitation_id]);

  const inviteMailChangeHandler = (event: any) => {
    setInviteMail(event.target.value);
  };

  const inviteWhatsappChangeHandler = (event: any) => {
    setInviteWhatsapp(event.target.value);
  };

  const refSucess = useRef<null | AddFunction>(null);
  const refError = useRef<null | AddFunction>(null);
  const handleCreateError = (msg: any) => {
    refError.current?.(msg);
  };
  const handleCreateSuccess = (msg: any) => {
    refSucess.current?.(msg);
  };

  const mutationCreate = useMutation(async (dataReq: InvitationType) => {
    const response = await SEND_INVITATION_LINK(
      dataReq,
      userState.userInfo.access
    );

    if (response === "404") {
      handleCreateError("Network response was not ok.");
    } else {
      handleCreateSuccess("Invitation has sent");
      return response;
    }
  });

  const inviteDataHandler = (event: any) => {
    let inviteMailArray = inviteMail.split(",");
    let inviteWhatsappArray = inviteWhatsapp.split(",");

    const data = {
      emails: inviteMailArray,
      invitation_link: inviteLink,
    };

    mutationCreate.mutateAsync(data);
  };

  return (
    <Container>
      <ErrorCatchComponent>
        {isLoading === false ? (
          <div>
            {isError ? (
              <ApiErrorPage
                error={
                  error ? error : new Error("Network response was not ok.")
                }
              />
            ) : (
              <Container maxWidth="xl">
                {isSuccess === true && data !== undefined ? (
                  <div>
                    <MessageError
                      children={(add: AddFunction) => {
                        refError.current = add;
                      }}
                    />
                    <MessageSuccess
                      children={(add: AddFunction) => {
                        refSucess.current = add;
                      }}
                    />

                    <Typography variant="h4">
                      Together we can help millions of people in the world to
                      <SpanText> achieve their goals!</SpanText>
                    </Typography>
                    <Box sx={{ p: 5, pl: 0, pb: 2 }}>
                      <TextField
                        fullWidth
                        required
                        multiline
                        id="outlined-required"
                        label="Invite Donee by Mail"
                        placeholder="Write the email of the person you want to invite"
                        onChange={inviteMailChangeHandler}
                      />
                    </Box>
                    <Divider sx={{ my: 3, p: 10, pb: 0, pt: 0 }}>
                      <Typography
                        variant="body2"
                        sx={{ color: "text.secondary" }}
                      >
                        OR
                      </Typography>
                    </Divider>
                    <Box sx={{ p: 5, pl: 0, pt: 2 }}>
                      <TextField
                        fullWidth
                        required
                        multiline
                        id="outlined-required"
                        label="Invite Donee by Whatsapp"
                        placeholder="Write the phone of the person you want to invite"
                        onChange={inviteWhatsappChangeHandler}
                      />
                      <Divider sx={{ my: 3, p: 10, pb: 0, pt: 0 }}>
                        <Typography
                          variant="body2"
                          sx={{ color: "text.secondary" }}
                        >
                          My invitation ID: {data.invitation_id}
                        </Typography>
                      </Divider>
                      <Divider sx={{ my: 3, p: 10, pb: 0, pt: 0 }}>
                        {inviteLink !== "" && (
                          <Typography
                            variant="body2"
                            sx={{ color: "text.secondary" }}
                          >
                            Invite Link: {inviteLink}
                          </Typography>
                        )}
                      </Divider>
                      {inviteLink !== "" && (
                        <Divider sx={{ my: 3, p: 10, pb: 0, pt: 0 }}>
                          <Stack
                            direction="row"
                            justifyContent="center"
                            alignItems="center"
                            spacing={2}
                          >
                            <FacebookShareButton
                              url={inviteLink}
                              quote={"You have been invited to join as Donee."}
                              className="Demo__some-network__share-button"
                            >
                              <FacebookIcon size={32} round />
                            </FacebookShareButton>
                            {/* <FacebookMessengerShareButton
                        url={inviteLink}
                        quote={"You have been invited to join as Donee."}
                        className="Demo__some-network__share-button"
                      >
                        <FacebookMessengerIcon size={32} round />
                      </FacebookMessengerShareButton> */}
                            <TwitterShareButton
                              url={inviteLink}
                              title={"You have been invited to join as Donee."}
                              className="Demo__some-network__share-button"
                            >
                              <TwitterIcon size={32} round />
                            </TwitterShareButton>
                            <WhatsappShareButton
                              url={inviteLink}
                              title={"You have been invited to join as Donee."}
                              className="Demo__some-network__share-button"
                            >
                              <WhatsappIcon size={32} round />
                            </WhatsappShareButton>
                          </Stack>
                        </Divider>
                      )}
                    </Box>

                    <Box
                      display="flex"
                      justifyContent="center"
                      alignItems="center"
                    >
                      {inviteLink !== "" && (
                        <Button
                          variant="contained"
                          color="secondary"
                          onClick={inviteDataHandler}
                        >
                          Invite Donee
                        </Button>
                      )}
                    </Box>
                  </div>
                ) : (
                  <ApiErrorPage
                    error={error ? error : new Error("Something went wrong.")}
                  />
                )}
              </Container>
            )}
          </div>
        ) : (
          <Loader />
        )}
      </ErrorCatchComponent>
    </Container>
  );
}

const SpanText = styled("span")(({ theme }) => ({
  color: " rgba(22, 182, 152, 0.65)",
}));
